export default {
	data() {
		return {
			imageBgUrl: 'http://39.108.65.247:8087/imgs/others/vin-bg.png',
			inputArr: []
		}
	},
	created() {
		this.initInput();
	},
	mounted() {
		// this.imageBgUrl = this.$Route.query.imgUrl;
	},
	methods: {
		initInput() {
			this.inputArr = [];
			for (let i = 0; i < 17; i++) {
				this.inputArr.push({
					value: '',
					num: i,
					focus: i === 0
				});
			}
		},
		inputHandle(index) {
			if (index < this.inputArr.length - 1) {
				this.inputArr[index + 1].focus = true;
			}
		},
		clearInputs() {
			this.initInput();
		}
	}
}
